<template>
	<div>
		<div class="inner">
			<h1 class="reg-title"></h1>
			<div class="form-list">
				<span class="contry-code fl">+86</span>
				<div class="bfc">
					<input type="tel" v-model="username" class="reg-input">
				</div>

			</div>
			<div class="form-list">
				<input type="password" v-model="password" class="reg-input" id="pwd">
			</div>
			<span class="reg-btn" @click="login('one')"></span>
			<span class="reg-btn reg-btn-red"></span>
			<div class="ta-r">
				<a href=""></a>
			</div>
		</div>
	</div>
</template>

<script>
	import Toast from 'vue-toast-mobile';

  export default {
    data () {
      return {
        username: '',
				password: ''
			}
		},
    beforeRouteEnter(to, from, next) {
			document.body.className = 'reg-bg';
			next();
    },
    beforeRouteLeave(to, from, next) {
      document.body.className = '';
      next();
		},
		methods: {
      async login(type) {
        let self = this;

          let data = new FormData()
          data.append('type', '1');
          data.append('username', this.username);
          data.append('password', this.password);

          const loginAjax = await fetch(this.$api.login, {
            method: 'POST',
            body: data
          });
          let userData = await loginAjax.json();
          if(userData.code == '200'){
            this.$store.dispatch('UPDATE_USERINFO', userData.datas);
            localStorage.setItem('userinfo', JSON.stringify(userData.datas))
            let redirect = decodeURIComponent(this.$route.query.redirect || '/');
            self.$router.push({
              path: redirect
            });
					}else{
            Toast(userData.datas.error);
					}


			}
		}
  }
</script>

<style lang="stylus">
	@import "../../assets/css/styl/config.styl";
	.reg-title
		margin: px2rem(80) 0 px2rem(150) 0;
		font-size px2rem(60)
	.form-list
		height px2rem(135px)
		padding px2rem(40) 0 px2rem(20) 0
		border-bottom: 1px solid rgba(255,255,255,0.7);
	.reg-input
		background: none
		border:none
		color #fff
		font-size 18px
		width 100%
		padding 5px 0
	.contry-code
		background #fff;
		font-size 18px;
		padding: 4px 10px;
		color color-reg
		border-radius 3px
		margin-right 5px
	.reg-btn
		width 100%
		display block
		border 1px solid #fff
		border-radius 50px
		font-size 18px
		text-align center
		padding 10px 0
		margin px2rem(40) 0
		&.reg-btn-red
			background color-red
			color #fff;
			border 1px solid color-red
</style>


